    margin: 0;
    padding: 0;
    box-sizing: border-box;
    width: 100%;    
    font-family: 'Playfair Display', 'Roboto', sans-serif;
    font-size: clamp(8px, 5vw, 20px);
}

body {
    display: flex;
    flex-direction:column;
    background-color: antiquewhite;
    align-items: center;
    margin: 0;
}

header {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 auto;
    width: 100%;
}

#about-panel {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
}

#profile-pic-holder {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-sizing: border-box;
    background: linear-gradient(160deg, rgb(231, 168, 231) 60%, antiquewhite 40%);
    width: 100%;
    padding-top: 2rem;
    margin-bottom: 2rem;
    
}

#ollie-profile-pic {
    width: 80%;
    height: auto;
    object-fit:cover;
}

#about-panel span {
    position: absolute;
    font-size:3rem;
    color: white;
    bottom: 2rem;
    width: 70%;
}

.profile-bio-container {
    padding: 1rem;
}

#about-title {
    padding-left: 1rem;
}

#profile-text {
    padding: 1rem;
    max-width: 90%;
    margin: 0;
    font-size: 1.5rem;
}


.social-medium {
    display: flex;
    width: 6rem;
    justify-content: space-around;
    margin-top: 1rem;
}

.social-medium img {
    width: 2rem;
    height: auto;

}


h1 {
    margin: 0;
}

main {
    display: flex;
    flex-direction:column;
    background-color: antiquewhite;
    align-items: center;
    flex-wrap: wrap;
    width: 80%;
    gap: 1rem;
    margin: 5rem auto;
}

#container-projects {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    row-gap: 5rem;
}

.project-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    border: none;
    box-shadow: 4px 4px 3px rgba(0, 0, 0, 0.1);
    max-width: 15rem;   /* allows flexbox wrap to adjust columns */
    background-color: white;
}

.card-top {
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    font-size:2rem;
    height: 15rem;
    width: 100%-1rem;
    padding-left: 1rem;
}

.card-bottom-header {
    display: flex;
    align-items: baseline;
    justify-content: space-between;
    padding-left: 1rem;
    padding-right: 1rem;
}

.card-social {
    display: flex;
    width: 3rem;
    justify-content: space-evenly;
}

.card-social img {
    width: 1rem;
}

.card-social svg {
    width: 1rem;
    height: 1rem;
}

.card-bottom p {
    width: 100% -2rem;
    padding-left: 1rem;
    padding-right: 1rem;
}


footer {
    display: flex;
    flex-direction: column;
    /* padding: 1rem; */
    align-items: center;
    background-color: rgb(231, 168, 231);
    width: 100%;
}
footer h1 {
    padding-left: 1rem;
}

.contact-container {
    padding: 2rem;
}

#ollie-exit-pic {
    width: 100%;
    height: auto;
    object-fit:cover;

}

#contact-info {
    padding: 1rem;
}

#contact-info p {
    font-size: normal;
}


#contact-info p:first-child {
    font-size: larger;
}


.social-largest {
    display: flex;
    width: 10rem;
    justify-content: space-around;
    margin-bottom: 4rem;
}

.social-largest img {
    width: 4rem;
    height: auto;
    
}

@media (min-width:600px) and (max-width: 1020px) {
    
    main {
        margin: 2rem auto;
        width: 90%;
    }
    header {
        background: linear-gradient(160deg, rgb(231, 168, 231) 60%, antiquewhite 40%);
        box-sizing: border-box;
        display: flex;
        padding: 2rem;
        padding-top: 8rem;
    }
    #about-panel {
        position: relative;
        display: block;
        background: white;
        border: 1 solid black;
        width: 80%;
        box-sizing: border-box;
        /* gap: 1rem; */
    }      
    #ollie-profile-pic {
        position: absolute;
        top: -5rem;
        left: -2rem;
        width: 100%;
        height: auto;
        object-fit:cover;
    }
    #profile-pic-holder {
        float: left;
        width: 60%;
        height: calc(60vw *1.3 - 14rem);
        background: none;
    }
    #profile-pic-holder span {
        position: absolute;
        font-size:3rem;
        color: white;
        top: -4rem;
        right: -15rem;
        min-width: 400px;
    }
    #about-title {
        margin-top: 2rem;
        margin-bottom: 0rem;
    }
    .social-medium {
        position: relative;
        width: 100%;
        justify-content: center;
        gap: 2rem;
        top: 3.5rem;
        /* left: calc(50vw - 10rem); */
        /* transform: translateX(-150%);  */
    }

    #profile-text {
        font-size: 1rem;
        padding: 1rem 2rem 0rem 2rem;
    }
    #my-work {
        align-self: flex-start;
    }   
    
    .project-card {
        max-width: 16rem;
    }
    
    .social-largest {
        margin-bottom: 2rem;
    }
    #contact-info {
        padding-top: 0rem;
    }

}

